*{

    margin: 0;
    padding: 0%;
    box-sizing: border-box;
}
.container-fluid{
    width: 90%;
    margin: auto;
}
.table-responsive{
    max-height:500px;
    box-shadow: 5px 5px 8px rgb(20, 20, 20), 10px 10px 8px rgb(51, 50, 50), 15px 15px 8px rgb(29, 26, 26);
}
.table thead th{
  max-height: 58px;
  background:rgb(5, 5, 5);
  color: white;
  position: sticky;
  top: 0;
}
td{
    position: relative;
}
.table td
.table th{
    padding: 0.7rem;
    vertical-align: top;
    border-top: none;
}
th:hover::after,
td:hover::after{
    content: "";
    position:absolute;
    background-color: #f2f2f2;
    left:0;
    top:-5000px;
    height: 5000px;
    width: 100%;
    z-index: -1;

}
.table tr td:hover:nth-child(4)
{
    background-color: rgb(231,83,115);
    color: aliceblue;
    border: 0.5px solid rgb(12,12,12);
}
.table tr td:hover{
    background-color: rgb(117,223,122);
    color:aliceblue;
    border: 0.5px solid rgb(12,12,12);
}
.thead-dark{
    font-weight: bold;
    text-transform: uppercase;
}
