*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #d2dbdd;
}
.header
{
  display: flex;
  
  align-items: center;
  height: 68px;
  background-color: brown;
  
}
.header img{
  margin: 8px;
  border-radius: 8px;
}
.header h1{
  margin-left: 8px;
  background-color: transparent;
}
footer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 83vh;
  background-color: thistle;
  height: 58px;
}
footer p{
  background-color: transparent;
}
.main_note{
    
    
    
    width:30vw;
    height: 40vh;
    margin-top:40px;
    margin-left: 35vw;
    box-shadow: 5px 5px 8px 10px #888888;
}
.form_grp{
  width: 50%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  flex-direction: column;
  margin-left:25%;
  margin-top:20%;
}
.form_grp input{
  border: none;
  border-bottom: 2px solid black;
  padding:8px;
  font-size: 18px; 
}
.form_grp textarea{
border: none;
border-bottom: 2px solid black;
padding:8px;
font-size:14px;
margin-left: -40px;
margin-top: 6px;
}
.form_grp button{
  margin-left: 83%;
  margin-top:-25px;
 
  box-shadow: 2px 2px 4px 5px #888888;
  color:rgb(209, 209, 87);
 
  border: none;
  display: block;
}
.note{
  margin-top: 20px;
  width: 20vw;
  height: 18vh;
  margin-left: 10px;
  box-shadow: 5px 5px 8px 10px #888888;
}
.note button{
  margin-left:88%;
  margin-top: 8%;
  border:none;
  box-shadow: 2px 2px 4px 5px #888888;
  color: rgb(211, 211, 76);
}
.active_class{
  color:rgb(172, 128, 71);
  border-radius: 1px solid red;
}



















































































